import React, {PropTypes} from 'react';
import style from './app.scss';
import { AppBar, IconButton, Layout, NavDrawer, Panel, Checkbox, List, ListSubHeader, ListItem,
    IconMenu, MenuItem, Button, DatePicker, Input} from 'react-toolbox';

class App extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      drawerActive: true,
      sidebarPinned: false,
      check1: true,
      check2: false,
      date1: new Date()
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange = (field, value) => {
    this.setState({[field]: value});
  };

  toggleDrawerActive = () => {
    this.setState({ drawerActive: !this.state.drawerActive });
  };

  toggleDrawerPinned = () => {
    this.setState({ drawerPinned: !this.state.drawerPinned });
  };

  render() {
    return (
      <Layout>
        <NavDrawer
          active={this.state.drawerActive}
          pinned={this.state.drawerActive}
          onOverlayClick={this.toggleDrawerActive}>

          <List selectable ripple>
            <ListSubHeader caption="Explore characters" />
            <ListItem caption="Начало" leftIcon="add" />
            <ListItem caption="Account" leftIcon="favorite" />
            <ListItem caption="Adv" leftIcon="star" />
            <ListItem caption="Auth" leftIcon="send" />
            <ListItem caption="Djcelery" leftIcon="send" />
            <ListItem caption="Sites" leftIcon="send" />
            <ListItem caption="Vkproxy" leftIcon="send" />
            <ListItem caption="Object state log" leftIcon="send" />
            <ListItem caption="Sync log" leftIcon="send" />
            <ListItem caption="Начало" leftIcon="add" />
            <ListItem caption="Account" leftIcon="favorite" />
            <ListItem caption="Adv" leftIcon="star" />
            <ListItem caption="Auth" leftIcon="send" />
            <ListItem caption="Djcelery" leftIcon="send" />
            <ListItem caption="Sites" leftIcon="send" />
            <ListItem caption="Vkproxy" leftIcon="send" />
            <ListItem caption="Object state log" leftIcon="send" />
            <ListItem caption="Sync log" leftIcon="send" />
          </List>
        </NavDrawer>
        <Panel>
          <AppBar>
            <IconButton
              icon={this.state.drawerActive ? "close" : "menu"}
              inverse={true}
              onClick={this.toggleDrawerActive}/>
            <h2 className={style.ml20}>
              Админка&nbsp;2.0
            </h2>
            <div className={style.toRight}>
              <IconMenu position="topRight" menuRipple>
                <MenuItem value="Рамиль" icon="favorite" caption="Рамиль" />
                <MenuItem value="Выйти" icon="favorite" caption="Выйти" />
              </IconMenu>
            </div>
          </AppBar>
          <section className={style.wrapper}>
            <h1 className={style.h1}>Версия с использованием React-toolbox (Material UI)</h1>
            <section>
              <Input type="text" label="Name" name="name" value={this.state.name} onChange={this.handleChange.bind(this, "name")} maxLength={16} />
              <Input type="text" hint="With Hint, no label" name="name" value={this.state.label} onChange={this.handleChange.bind(this, "label")} maxLength={16} />
              <Input type="text" label="Disabled field" disabled />
              <Input type="text" multiline label="Multiline" maxLength={20} value={this.state.multiline} onChange={this.handleChange.bind(this, "multiline")} />
              <Input type="email" label="Email address" icon="email" value={this.state.email} onChange={this.handleChange.bind(this, "email")} />
              <Input type="tel" label="Phone" name="phone" icon="phone" value={this.state.phone} onChange={this.handleChange.bind(this, "phone")} />
              <Input type="text" value={this.state.hint} label="Required Field" hint="With Hint" required onChange={this.handleChange.bind(this, "hint")} icon="share" />
            </section>
            <div>
              <Checkbox
                checked={this.state.check1}
                label="Checked option"
                onChange={this.handleChange.bind(this, "check1")}
              />
              <Checkbox
                checked={this.state.check2}
                label="Unchecked option"
                onChange={this.handleChange.bind(this, "check2")}
              />
              <Checkbox
                checked
                disabled
                label="Disabled checkbox"
              />
            </div>

            <DatePicker
              label="Birthdate"
              onChange={this.handleChange.bind(this, "date1")}
              value={this.state.date1}
              sundayFirstDayOfWeek
            />
            <div><br />
              <Button label="Send" accent primary raised />
            </div>
          </section>
        </Panel>
      </Layout>
    );
  }
}

App.propTypes = {
  children: PropTypes.object.isRequired
};

export default App;



